<template>
  <main class="new-estate">
    <section class="page-title-section theme-bg dashboard-title">
      <div class="container dashboard-modified"></div>
    </section>
    <div class="container mt-3 min-80vh">
        
      <form-wizard
        title="Top Up"
        subtitle="Please fill the fields carefully"
        color="#193870"
        errorColor="#e81c1c"
        @on-loading="setLoading"
        @on-complete="onComplete"
      >
        <tab-content title="Purchase Information" :before-change="validateAsync" icon="ti-clipboard">
          
          <div class="row justify-content-center">
            <div class="col-md-5 mb-2">
              <input 
                type="text" 
                disabled="true"
                class="form-control"
                v-model="meterData.meterType"
                placeholder="Purchase Type"
              >
            </div>
            <div class="clearfix"></div>
            <div class="invalid-feedback block text-center">{{errors.meterType}}</div>
          </div>
          <div class="row justify-content-center">
            <div class="col-md-5 mb-2">
              <input
                type="text"
                class="form-control"
                 disabled="true"
                v-model="meterData.meterNumber" 
                placeholder="Meter Number"
              >
            </div>
            <div class="clearfix"></div>
            <div class="invalid-feedback block text-center">{{errors.meterNumber}}</div>
          </div>
          <div class="row justify-content-center">
            <div class="col-md-5 mb-2">
              <input
                type="text"
                class="form-control"
                 disabled="true"
                v-model="meterData.userId.email"
                placeholder="Email"
              >
              <div class="clearfix"></div>
              <div class="invalid-feedback block text-center">{{errors.email}}</div>
            </div>
          </div>
          <div class="row justify-content-center">
            <div class="col-md-5 mb-2">
              <div class="input-group modified">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text" id="basic-addon1">&#8358;</span>
                                    </div>
                                    <input
                                        type="number"
                                        class="form-control no_btns font-18px"
                                        placeholder="Amount" 
                                        v-model="meterData.amountToPay" />
                                </div>
            </div>
            <div class="clearfix"></div>
            <div class="invalid-feedback block text-center">{{errors.amountToPay}}</div>
          </div>
        </tab-content>
        <tab-content title="Your order" icon="ti-money" :before-change="validateAsync">
          <div class="row justify-content-center">
            <div class="col-md-5 mb-2">
              <label for="waterUnitRate">Purchase type: {{meterData.meterType}}</label>
            </div>
          </div>
            <div class="row justify-content-center">
            <div class="col-md-5 mb-2">
              <label for="waterUnitRate">Amount: N{{meterData.amountToPay}}</label>
            </div>
            </div> 
            <div class="row justify-content-center">
            <div class="col-md-5 mb-2">
              <label for="waterUnitRate">Email: {{meterData.userId.email}}</label>
            </div>
          </div>
          <div class="row justify-content-center">
            <div class="col-md-5 mb-2">
              <label for="electricityUnitRate">Rate: N {{estateData.waterUnitRate}} </label>
              
            </div>
          </div>
        </tab-content>
        
      </form-wizard>
    </div>
     <PayStack 
            :amount="Number(payStackData.amount)"
            :email="payStackData.email"
            :paystackkey="payStackData.paystackkey"
            :reference="payStackData.reference"
            :callback="callback"
            :close="close"
            :embed="false"
            v-on:hidePopUp="hideAllPopups"
        >
        </PayStack> 
  </main>
</template>

<script>
import { FormWizard, TabContent } from "vue-form-wizard";
import "../../assets/static/css/form_wizard.css";
import * as auth from "../../services/auth_service";
import * as estate from "../../services/estate_service";
import PayStack from '@/components/PayStack.vue';
import * as sendSMS from "../../services/sms_service";

export default {
  name: "anonymous",
  components: {
    FormWizard,
    TabContent,
    PayStack
  },
  data() {
    return {
        meterData:{},
      estateData: {
        loadingWizard: false,
        estateName: "",
        companyName: "",
        address: "",
        state: "",
        waterUnitRate: 80,
        electricityUnitRate: "",
        otherBillableServices: []
      },
      // paystack stuffs
                payStackData : {
                    paystackkey: "pk_test_8cc88893ea0334eb8432a399851134a919f6907f",
                    email: '', 
                    amount: Number, // in kobo
                    reference: '',
                },

      service: {
        serviceName: ""
      },
      errors: []
    };
  },
  created() {
    //this.meterData = this.$route.params.meterData;
    
    if (!this.$route.params.meterData) {
        this.flashMessage.warning({
          title: "Meter Number check",
          message: "Please provide a meter number",
          time: 5000,
          icon: "../../assets/static/img/water_drop.svg"
        });
      this.$router.push("/");
    } else {
      this.meterData = this.$route.params.meterData;
      console.log(this.$route.params.meterData)
      //return this.meterData;
    }
  },
  methods: {
    addBillableService: function() {
      this.estateData.otherBillableServices.push({
        serviceName: "",
        serviceAmount: ""
      });
    },
    setLoading: function(value) {
      this.loadingWizard = value;
    },
    hideAllPopups() {
                //this.hideWaterMeterModal();
               // this.hideGasMeterModal();
                //this.hideElectricityMeterModal();
            },
    callback: async function(response){
                if (this.meterType === 'water') {
                    const serial_id = '0000001';
                    const user_id = 'DCESN';
                    const meter_id = '47000123274';
                    const token_type = 'CreditToken';
                    const amount = '1.0';
                    const key = '399f0d868b264474a2ad093d7ef530f7';
                    const timestamp = moment().unix();
                    const ciphertext = md5(String(serial_id) + user_id + meter_id + token_type + amount + timestamp + key
                    );

                    const tokenData = { serial_id, user_id, meter_id, token_type, amount, timestamp, ciphertext };

                    try {
                        const {data} = await meter.generateTokenWater(tokenData);
                        
                        response.tokenData = data.tokenData
                        response.meterType = this.meterType;
                        response.meterNumber = this.meterNumber;
                        response.amount = this.totalAmount;

                        this.smsMessageData.tokenData= data.tokenData
                        this.smsMessageData.phoneNumber = "08056441978"
                        
                        this.sendSMS(this.smsMessageData);
                        
                        const storeTransaction = await transaction.storeTransaction(response);

                        this.validateFirstStepCheckWater = false;
                        this.loadTransactions();
                        
                    } catch (error) {
                        this.flashMessage.error({
                            title: 'Some error occured',
                            message: 'Please reload the page or use another internet service provider and try again!',
                            time: 8000,
                            icon: '../../assets/static/img/water_drop.svg'
                        });
                    }
                }else if (this.meterType === 'electricity') {
                    const tokenData = { 
                        transactionId: this.electricityMeterOwnerInfo.transactionId, 
                        amount: this.totalAmountToPay,
                        access_token: this.electricityMeterOwnerInfo.access_token
                    };

                    try {
                        const {data} = await meter.generateTokenElectricity(tokenData);
                        
                        response.tokenData = {
                            result: data.tokenData.data.token,
                            transaction: data.tokenData.data.transactionId
                            

                        };
                        //preparing SMS
                         this.smsMessageData.tokenData= JSON.stringify(data.tokenData.result)
                        this.smsMessageData.phoneNumber = "08056441978"
                        this.sendSMS(this.smsMessageData);

                        

                        response.transactionType = this.electricityMeterOwnerInfo.meterType;
                        response.discoType = this.electricityMeterOwnerInfo.discoType;
                        response.meterType = this.meterType;
                        response.meterNumber = this.meterNumber;
                        response.amount = this.totalAmount;
                        const storeTransaction = await transaction.storeTransaction(response);

                        this.validateFirstStepCheckElectricity = false;
                        this.loadTransactions();
                        

                    } catch (error) {
                        console.log(error)
                        this.flashMessage.error({
                            title: 'Transaction Failed',
                            message: 'Some error occured, Please reload the page and try again!',
                            time: 5000,
                            icon: '../../assets/static/img/water_drop.svg'
                        });
                    }
                }
                
                this.generateReference();
            },
            close: function(){
                // console.log("Payment closed")
            },
            sendSMS:async function(){
                try {
                    //console.log("In try Statement")
                    
                    await sendSMS.sendSMS(this.smsMessageData);
                } catch (error) {
                    console.log(error)
                        this.flashMessage.error({
                            title: 'SMS Not sent',
                            message: 'Some error occured, contact admin!',
                            time: 5000,
                            icon: '../../assets/static/img/water_drop.svg'
                        });
                    
                }
            },
    validateAsync: function() {
       //console.log("Checking length "+JSON.stringify(this.meterData))
        
      this.errors = [];
      return new Promise((resolve, reject) => {
      
        if (this.meterData.amountToPay < 1000 || this.meterData.amountToPay === undefined) {
          
          this.errors.amountToPay = "Amount cannot be empty or less than N1000";
          reject(true);
        } else {
          resolve(true);
        }
       

        
      });
    },
 handlePaymentRequest(meterType, meterNumber) {
                this.meterType = this.meterData.meterType;
                this.meterNumber =   this.meterData.meterNumber;
                this.$store.state.userEmail = this.meterData.userId.email;
                this.errors = [];
                const amount = parseInt(this.meterData.amountToPay);
                if (isNaN(amount) || amount < 1000 || this.validateFirstStepCheck === false) {
                    this.errors.amountToPay = 'You should enter at least 1000 amount';
                    return;
                }

                this.payStackData.amount = amount*100;
                this.payStackData.email = this.$store.state.userEmail;
                this.$root.$emit('openPayStack');
                
            },
    onComplete: async function() {
    this.handlePaymentRequest();

      // this.addBillableService();
      // this.errors = [];
      // if (this.estateData.estateName.length < 2) {
      //   this.errors.estateName = "Please name of estate";

      //   return;
      // }

      // try {
      //   //console.log("View Message : Saved!" + this.estateData.estateName)
      //   const { data } = await estate.storeEstateData(this.estateData);
      //   this.flashMessage.success({
      //     title: "New estate added",
      //     message: this.estateData.estateName + " has been saved!",
      //     time: 5000,
      //     icon: "../../assets/static/img/water_drop.svg"
      //   });
      //    this.$router.push({name:'estates'})
       
      // } catch (error) {
      //   this.flashMessage.error({
      //     title: "There was a problem saving the estate",
      //     message: "Kindly check your estate name!",
      //     time: 5000,
      //     icon: "../../assets/static/img/water_drop.svg"
      //   });
      // }
    }
  }
};
</script>

